<template>
  <div class="flex flex-col">
    <div class=" m-2 flex justify-between">
      <div class="flex">
        <input
          ref="txtdni"
          type="number"
          class=" p-3 bg-gray-100 rounded-xl"
          placeholder="Dni"
          maxlength="8"
          v-model="dni"
          @input="$emit('input', dni)"
        />
        <vs-button @click="clickClean()">
          <li class="fas fa-sync"></li>
        </vs-button>
      </div>

      <!-- <div class="">
        <vs-button class=" focus:outline-none" @click="get_links_fotos_zip()">
          Descargar Fotos
        </vs-button>

        <ul
          class=" bg-gray-500 text-white p-3 absolute right-10 z-10 rounded-3xl"
          v-if="ver_menu"
        >
          <li
            v-for="(item, i) in fotos_links"
            :key="i"
            class="hover:text-gray-400 w-full text-xs my-1"
          >
            <a :href="`${base_photo}${item.zip}`" target="_blank">
              {{ item.name }}
            </a>
          </li>
        </ul>
      </div> -->
    </div>

    <div class="text-center flex flex-col lg:flex-row lg:flex-wrap">
      <div
        class=" relative flex flex-col mx-2 border p-3 rounded-lg mt-2 lg:mt-0"
        v-for="(item, i) in get_dataList"
        :key="i"
      >
        <div
          class=" flex flex-col text-left text-xs w-full"
          style="width: 240px !important;"
        >
          <span
            class=" text-lg underline font-medium uppercase cursor-pointer"
            @click="$emit('edit', item)"
            >{{
              `${item.apellido_paterno} ${item.apellido_materno} ${item.nombre}`
            }}</span
          >

          <span class=" font-medium mt-2">
            <i class="fas fa-user text-green-500 mr-2"></i>
            {{ item.dni }}</span
          >

          <div class=" flex justify-between">
            <div></div>
            <span class=" text-right cursor-pointer w-auto">
              <i class="fas fa-phone mr-2 text-blue-500"></i>
              {{ item.celular }}
            </span>
          </div>

          <span :class="item.validado ? ' text-green-500' : ' text-red-500'">
            {{ item.validado ? " Validado" : " Sin Validar" }}
          </span>
        </div>

        <div
          class="flex justify-center items-center text-center mt-2 cursor-pointer"
        >
          <img :src="`${base_photo}${item.photo}`" class="foto" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    dataList: {
      type: Array,
    },
  },

  data() {
    return {
      dni: "",
      fotos_links: [],
      ver_menu: false,
    };
  },

  computed: {
    get_dataList() {
      return this.dataList.filter((item) => {
        return item.dni.includes(this.dni);
      });
    },

    base_photo() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/media/");
      return host;
    },
  },

  methods: {
    get_links_fotos_zip() {
      this.ver_menu = !this.ver_menu;
      this.$store.dispatch("postulante/get_links_fotos").then((resp) => {
        this.fotos_links = resp;
      });
    },

    clickClean() {
      this.dni = "";
      this.$refs.txtdni.focus();
    },
  },
};
</script>

<style lang="scss" scoped>
.foto {
  width: 240px !important;
  height: 288px !important;
}
</style>
