<template>
  <section class="flex flex-col items-center p-3 shadow-md rounded-lg w-full">
    <div class="flex justify-center items-center text-center">
      <img
        :src="foto"
        class=" bg-center w-full"
        style="width: 240px !important; high: 288px !important;"
      />
    </div>

    <div class="mt-3 w-full">
      <div class=" text-center">
        <h1 class=" text-2xl">
          <b>{{
            `${item_selected.nombre} ${item_selected.apellido_paterno} ${item_selected.apellido_materno}`
          }}</b>
        </h1>
        <div class=" flex flex-col">
          <h2>
            Dni: <span class=" font-medium">{{ item_selected.dni }}</span>
          </h2>
          <span
            >Edad:
            <span class=" font-medium">{{ item_selected.edad }}</span></span
          >
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {
      foto: null,
    };
  },

  watch: {
    item_selected(newValue) {
      if (newValue) this.get_foto();
    },
  },

  methods: {
    async get_foto() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/");

      host += `media/${this.item_selected.photo}`;

      //
      this.foto = host;
    },
  },
};
</script>

