<template>
  <section
    class="flex flex-col items-start p-3 shadow-md rounded-lg w-full bg-white"
  >
    <h1 class=" text-xl font-bold text-green-500">
      Información del Postulante
    </h1>
    <div class="mt-7">
      <h1 class=" text-lg text-blue-500 font-medium">Información Personal</h1>
      <div>
        <span class=" text-xs ml-2">Apellido Paterno</span>
        <input
          v-model="apellido_paterno"
          class="w-full border p-2 px-3 rounded-xl"
          type="text"
        />

        <span class=" text-xs ml-2">Apellido Materno</span>
        <input
          v-model="apellido_materno"
          class="w-full border p-2 px-3 rounded-xl"
          type="text"
        />

        <span class=" text-xs ml-2">Nombres</span>
        <input
          v-model="nombre"
          class="w-full border p-2 px-3 rounded-xl"
          type="text"
        />
      </div>

      <div class="mt-7">
        <vs-input
          label="Genero"
          :value="item_selected.genero"
          class="w-full"
          readonly
        />
      </div>

      <div class="flex flex-col mt-2">
        <span class=" text-xs ml-2">Dni</span>
        <input
          v-model="dni"
          class="w-64 border p-2 px-3 rounded-xl"
          type="text"
          readonly
          disable
        />
      </div>
    </div>

    <div class="mt-7 w-full">
      <h1 class=" text-lg text-blue-500 font-medium">
        Información de Nacimiento
      </h1>
      <div>
        <span class=" text-xs ml-2">Fecha de Nacimiento</span>
        <input
          :value="item_selected.fecha_nacimiento"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl"
          readonly
          type="text"
        />

        <span class=" text-xs ml-2">Lugar de Nacimiento</span>
        <input
          :value="item_selected.ubigeo_nacimiento"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
          readonly
          type="text"
        />
      </div>
    </div>

    <div class="mt-7 w-full">
      <h1 class=" text-lg text-blue-500 font-medium">
        Dirección Actual
      </h1>
      <div>
        <span class=" text-xs ml-2">Dirección</span>
        <input
          :value="item_selected.direccion"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
          readonly
          type="text"
        />

        <span class=" text-xs ml-2">Ubicación Actual</span>
        <input
          :value="item_selected.ubigeo"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
          readonly
          type="text"
        />
      </div>
    </div>

    <div class="mt-7 w-full">
      <h1 class=" text-lg text-blue-500 font-medium">
        Información de Contacto
      </h1>
      <div class=" flex flex-col">
        <span class=" text-xs ml-2">Telefono</span>
        <input
          v-model="telefono"
          class="w-1/2 border p-2 px-3 rounded-xl"
          type="text"
        />

        <span class=" text-xs ml-2">Celular</span>
        <input
          v-model="celular"
          class="w-1/2 border p-2 px-3 rounded-xl"
          type="text"
        />

        <span class=" text-xs ml-2">Correo Electrónico</span>
        <input
          v-model="correo"
          class="w-full border p-2 px-3 rounded-xl"
          readonly
          type="text"
        />
      </div>

      <div class="mt-2">
        <vs-button @click="modificarPostulante()">
          Guardar Cambios
        </vs-button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {
      nombre: "",
      apellido_paterno: "",
      apellido_materno: "",
      dni: "",
      telefono: "",
      celular: "",
      correo: "",
    };
  },

  mounted() {
    const newValue = this.item_selected;
    if (newValue) {
      this.nombre = newValue.nombre;
      this.apellido_paterno = newValue.apellido_paterno;
      this.apellido_materno = newValue.apellido_materno;
      this.dni = newValue.dni;
      this.telefono = newValue.telefono;
      this.celular = newValue.celular;
      this.correo = newValue.correo;
    }
  },

  methods: {
    validarData() {
      if (
        this.dni.length == 0 ||
        this.apellido_paterno.length == 0 ||
        this.apellido_materno.length == 0 ||
        this.nombre.length == 0 ||
        this.celular.length == 0 ||
        this.correo.length == 0
      ) {
        this.$vs.notification({
          title: "Postulante",
          text: "Verifica los campos obligatorios! 😒",
          color: "danger",
        });
        return false;
      }

      return true;
    },

    async modificarPostulante() {
      if (!this.validarData()) return;

      const loading = this.$vs.loading();
      try {
        const params = {
          nombre: this.nombre,
          apellido_paterno: this.apellido_paterno,
          apellido_materno: this.apellido_materno,
          telefono: this.telefono,
          celular: this.celular,
          correo: this.correo,
          id: this.item_selected.id,
        };

        const resp = await this.$store.dispatch(
          "postulante/modificarPostulante",
          params
        );

        if (resp != 200) {
          this.$vs.notification({
            title: "Postulante",
            text: "No se pudo modificar! 😢",
            color: "danger",
          });
          return;
        }

        this.$emit("actualizar");

        this.$vs.notification({
          title: "Postulante",
          text: "Postulante modificado correctamente!😎",
          color: "success",
        });
      } catch (error) {
        this.$vs.notification({
          title: "Postulante",
          text: "No se pudo modificar! 😢",
          color: "danger",
        });
      } finally {
        loading.close();
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
