<template>
  <div>
    <vs-button
      @click="printRegistro()"
      class=" focus:outline-none"
      border
      danger
    >
      <i class="fas fa-file-pdf mr-2"></i> Imprimir Registro
    </vs-button>
  </div>
</template>

<script>
export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  methods: {
    printRegistro() {
      let routeData = this.$router.resolve(
        `/reporte/${this.item_selected.id}`
      );

      window.open(routeData.href, "_blank");
    },
  },
};
</script>