<template>
  <div>
    <vs-button @click="printRegistro()" class=" focus:outline-none" border dark>
      <i class="fas fa-file-pdf mr-2"></i> Imprimir Declaración Jurada
    </vs-button>
  </div>
</template>

<script>
import axios from "@//plugins//axios.js";

export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {};
  },

  computed: {
    base_media() {
      let host =
          process.env.NODE_ENV == "development"
              ? process.env.VUE_APP_BASE_URL
              : process.env.VUE_APP_URL;

      host = host.replace("api/v1/", "")
      return host;
    },
  },

  methods: {
    async printRegistro() {


      const {data} = await axios.get("reportes/get-declaracion-jurada", {
        params: {id: this.item_selected.id}
      })

      if(!data){
        alert("Ocurrio un problema")
        return
      }

      var a = document.createElement("a");
      a.href = this.base_media + data.file
      a.target = "_blank"
      a.click()
      return
    },
  },
};
</script>
