<template>
  <vs-dialog id="dialog" v-model="dialogLocal" not-center width="700px">
    <template #header>
      <div class="flex flex-col">
        <h1><b>Vista de Postulante</b></h1>
        <span class="text-xs font-bold"
          >Fecha Recepción:
          {{ item_selected_local.fecha_recepcion || "" }}</span
        >
      </div>
    </template>

    <div>
      <perfil :item_selected="item_selected_local" />
      <options @option="option = $event" />
    </div>

    <div class="mt-5">
      <requisito
        :item_selected="item_selected_local"
        v-if="Object.entries(item_selected_local).length > 0 && option == 0"
      />
      <info
        :item_selected="item_selected_local"
        v-if="option == 1"
        @actualizar="$emit('actualizar')"
      />
      <generals :item_selected="item_selected_local" v-if="option == 2" />
      <educativas :item_selected="item_selected_local" v-if="option == 3" />
      <programa :item_selected="item_selected_local" v-if="option == 4" />
    </div>
  </vs-dialog>
</template>

<script>
import Educativas from "./viewcomponents/Educativas.vue";
import Generals from "./viewcomponents/Generals.vue";
import Info from "./viewcomponents/Info.vue";
import Options from "./viewcomponents/Options.vue";
import Perfil from "./viewcomponents/Perfil.vue";
import Programa from "./viewcomponents/Programa.vue";
import Requisito from "./viewcomponents/Requisito.vue";

export default {
  props: {
    dialog: {
      type: Boolean,
    },

    item_selected: {
      type: Object,
    },
  },

  components: {
    Perfil,
    Info,
    Options,
    Generals,
    Educativas,
    Programa,
    Requisito,
  },

  data() {
    return {
      dialogLocal: false,
      option: 0,
      item_selected_local: {},
    };
  },

  watch: {
    dialog(newValue) {
      if (newValue) {
        this.dialogLocal = newValue;

        this.$store
          .dispatch("postulante/getInfoPostulante", this.item_selected.id)
          .then((resp) => {
            this.item_selected_local = resp;
          });
      }
    },

    dialogLocal(newValue) {
      if (!newValue) {
        this.option = 0;
        this.item_selected_local = {};
        this.$emit("closedialog");
      }
    },
  },
};
</script>

<style lang="scss">
#dialog > .vs-dialog {
  max-width: 100% !important;
  width: 60%;
}
</style>
