<template>
  <div class="center">
    <div class=" p-5 rounded-3xl bg-gray-200 w-32">
      <span
        >Total: <b class=" text-3xl">{{ dataListLocal.length }}</b></span
      >
    </div>

    <div class=" mt-2 flex">
      <vs-radio v-model="modo" val="lista">
        Lista
      </vs-radio>

      <vs-radio v-model="modo" val="foto">
        Foto
      </vs-radio>

      <vs-radio v-model="modo" val="validacion">
        Validación
      </vs-radio>
    </div>

    <div>
      <vs-table v-if="modo == 'lista'" striped class=" mt-2">
        <template #header>
          <div class=" mb-2 text-lg font-medium px-2">
            <h1>Filtros</h1>

            <div class=" w-1/12 flex items-center">
              <span for="">
                <vs-radio
                  v-model="limite"
                  val="true"
                  @input="$emit('limits', true)"
                  >10</vs-radio
                >
              </span>

              <span for="">
                <vs-radio
                  v-model="limite"
                  val="false"
                  @input="$emit('limits', false)"
                  >Todos</vs-radio
                >
              </span>
            </div>
          </div>

          <div class="bg-white p-3 rounded-3xl">
            <div class=" flex flex-col lg:flex-row">
              <div>
                <span class=" text-xs">Dni</span>

                <input
                  type="number"
                  id="txtdni"
                  class="p-3 bg-gray-100 rounded-xl block"
                  v-model.trim="filters.dni"
                  @input="$emit('filtering', filters)"
                />
              </div>

              <div class="pt-5">
                <vs-button
                  class=" w-full focus:outline-none"
                  @click="$emit('filtering', filters)"
                  >Buscar</vs-button
                >
              </div>
            </div>
          </div>

          <div
            v-if="
              get_auth.group == 'administrador' || get_auth.group == 'consultor'
            "
            class="flex flex-col mt-5 w-full bg-white p-3 rounded-xl"
          >
            <div class="flex  justify-between flex-wrap">
              <div class=" lg:w-1/3 w-full">
                <span class=" text-xs">Modalidad</span>
                <v-select
                  v-model="filters.modalidad"
                  :options="modalidad_data"
                  label="nombre"
                  index="id"
                  @input="modalidadSelected()"
                >
                </v-select>
              </div>

              <div class=" lg:w-1/3 w-full">
                <span class=" text-xs">Escuela</span>
                <v-select
                  v-model="filters.escuela"
                  :options="escuela_data"
                  label="nombre"
                  index="id"
                  @input="$emit('filtering', filters)"
                >
                </v-select>
              </div>

              <div class=" lg:w-1/3 w-full" v-if="!bloquear_filial">
                <span class=" text-xs">Sede/Filial</span>
                <v-select
                  v-model="filters.filial"
                  :options="sede_data"
                  label="nombre"
                  index="id"
                  @input="$emit('filtering', filters)"
                >
                </v-select>
              </div>

              <div class=" lg:w-1/3 w-full">
                <span class=" text-xs">Universidad Procedencia</span>
                <v-select
                  v-model="filters.universidad_procedencia"
                  :options="universidad_data"
                  label="nombre"
                  index="id"
                  @input="$emit('filtering', filters)"
                >
                </v-select>
              </div>

              <div class=" mt-2 flex">
                <vs-checkbox
                  success
                  :val="true"
                  v-model="filters.ingresantes"
                  @change="checkIngresantes"
                >
                  <template #icon>
                    <i class="fas fa-check-double"></i>
                  </template>
                  Solo ingresantes
                </vs-checkbox>

                <vs-button @click="$emit('imprimir', dataListLocal)">
                  Descargar Constancias
                </vs-button>
              </div>
            </div>

            <div class="flex flex-wrap">
              <div class="mt-2">
                <vs-button
                  success
                  class="focus:outline-none"
                  @click="exportarExcel"
                >
                  Exportar a Excel
                </vs-button>
              </div>

              <div class="mt-2">
                <vs-button
                  success
                  class="focus:outline-none"
                  @click="exportarFotos"
                >
                  Exportar Fotos
                </vs-button>
              </div>

              <div class="mt-2">
                <vs-button
                  dark
                  class="focus:outline-none"
                  @click="exportarDigraa"
                >
                  Formato Ingresantes (DIGRAA)
                </vs-button>
              </div>

              <div class="mt-2">
                <vs-button dark class="focus:outline-none" @click="exportF6">
                  Formato F6
                </vs-button>
              </div>
            </div>
          </div>
        </template>

        <template #thead>
          <vs-tr>
            <vs-th
              sort
              @click="dataListLocal = $vs.sortData($event, dataListLocal, 'id')"
            >
              Id
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData(
                  $event,
                  dataListLocal,
                  'apellido_paterno'
                )
              "
            >
              Apellido Paterno
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData(
                  $event,
                  dataListLocal,
                  'apellido_materno'
                )
              "
            >
              Apellido Materno
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'nombre')
              "
            >
              Nombre
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'dni')
              "
            >
              Dni
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'modalidad')
              "
            >
              Modalidad
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'escuela')
              "
            >
              Escuela
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'fecha')
              "
            >
              Fecha
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'total')
              "
            >
              Total
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'celular')
              "
            >
              Celular
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData($event, dataListLocal, 'validado')
              "
            >
              Validado
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData(
                  $event,
                  dataListLocal,
                  'cubrio_vacante'
                )
              "
            >
              Cubrio Vacante
            </vs-th>

            <vs-th
              sort
              @click="
                dataListLocal = $vs.sortData(
                  $event,
                  dataListLocal,
                  'nota_examen'
                )
              "
            >
              Puntaje
            </vs-th>
          </vs-tr>
        </template>

        <template #tbody>
          <vs-tr :key="i" v-for="(tr, i) in dataListLocal" :data="tr">
            <vs-td edit @click="$emit('edit', tr)">
              <span>{{ tr.id }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.apellido_paterno }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.apellido_materno }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.nombre }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.dni }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.modalidad }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.escuela }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.fecha }}</span>
            </vs-td>

            <vs-td>
              <span :class="{ ' line-through': tr.total == 0 }">{{
                ParseMoney(tr.total)
              }}</span>
            </vs-td>

            <vs-td>
              <span>{{ tr.celular }}</span>
            </vs-td>

            <vs-td>
              <span
                class="px-2"
                :class="{
                  'bg-green-500 text-white rounded-2xl': tr.validado,
                  'text-red-500 line-through': !tr.validado,
                }"
                >{{ tr.validado ? "Si" : "No" }}</span
              >
            </vs-td>

            <vs-td>
              <span
                :class="{
                  'bg-green-500 text-white rounded-2xl': tr.cubrio_vacante,
                  'text-red-500': !tr.cubrio_vacante,
                }"
                >{{ tr.cubrio_vacante_text }}</span
              >
            </vs-td>

            <vs-td>
              <span>{{ ParseMoney(tr.nota_examen) }}</span>
            </vs-td>
          </vs-tr>
        </template>

        <template #notFound>
          No existen datos 😢
        </template>
      </vs-table>

      <validar-documentos
        :dataList="dataListLocal"
        @input="
          (event) => {
            filters.dni = event;
            $emit('filtering', filters);
          }
        "
        @edit="$emit('edit', $event)"
        v-else-if="modo == 'validacion'"
      />

      <list-foto
        v-else
        :dataList="dataListLocal"
        @edit="$emit('edit', $event)"
        @input="
          (event) => {
            filters.dni = event;
            $emit('filtering', filters);
          }
        "
      />
    </div>
  </div>
</template>

<script>
import axios from "@//plugins//axios";
import ListFoto from "./ListFoto.vue";
import ValidarDocumentos from "./ValidarDocumentos.vue";

export default {
  components: { ListFoto, ValidarDocumentos },
  props: {
    dataList: {
      type: Array,
    },
  },

  data() {
    return {
      columns: [
        {
          name: "id",
          text: "Id",
        },

        {
          name: "apellido_paterno",
          text: "Apellido Paterno",
        },

        {
          name: "apellido_materno",
          text: "Apellido Materno",
        },

        {
          name: "nombre",
          text: "Nombre",
        },

        {
          name: "dni",
          text: "Dni",
        },

        {
          name: "modalidad",
          text: "Modalidad al que postula",
        },

        {
          name: "escuela",
          text: "Escuela al que postula",
        },

        {
          name: "fecha",
          text: "Fecha Preinscripción",
        },

        {
          name: "total",
          text: "Total",
        },

        {
          name: "celular",
          text: "Celular",
        },

        {
          name: "validado",
          text: "Validado",
        },

        {
          name: "cubrio_vacante",
          text: "Cubrio Vacante",
        },
      ],
      dataListLocal: [],
      filters: {
        modalidad: null,
        escuela: null,
        dni: null,
        filial: null,
        ingresantes: null,
        universidad_procedencia: "",
      },

      modalidad_data: [],
      universidad_data: [
        {
          id: 1,
          nombre: "UCP",
        },

        {
          id: 2,
          nombre: "UPO",
        },

        {
          id: 3,
          nombre: "UPS",
        },

        {
          id: 4,
          nombre: "TODOS",
        },
      ],
      escuela_data: [],
      sede_data: [],
      limite: "true",
      bloquear_filial: false,
      codigo_barra: null,
      modo_lista: true,
      modo: "lista",
    };
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },
  },

  watch: {
    dataList(newValue) {
      this.dataListLocal = newValue;
    },
  },

  async mounted() {
    this.set_modalidad();
    this.set_escuela();
    await this.set_sede();
    this.filterFilial();

    setTimeout(() => {
      document.getElementById("txtdni").focus();
    }, 200);
  },

  methods: {
    async modalidadSelected() {
      try {
        if (!this.filters.modalidad) {
          this.set_escuela();
          return;
        }

        const { data } = await axios.get("getEscuelasBySedeModalidad", {
          params: {
            sede: 1,
            modalidad: this.filters.modalidad.id,
          },
        });

        this.escuela_data = data.map((row) => {
          return { id: row.escuela, nombre: row.nombre };
        });
        this.$emit("filtering", this.filters);
      } catch (error) {
        return null;
      }
    },

    async exportF6() {
      const loading = this.$vs.loading({
        color: "success",
        text: "Generando archivo, tome un café mientras espera☕☕",
      });
      try {
        const { data, status } = await axios.get(
          "get_postulante_results_to_excel"
        );

        if (status == 200) {
          var img = document.createElement("a");
          img.href = this.base_photo() + data.file;
          img.click();
        }
      } catch (error) {
        alert("Ocurrio un problema!");
      } finally {
        loading.close();
      }
    },

    async exportarDigraa() {
      try {
        const { data, status } = await axios.get("getReporteIngresantesDigraa");

        if (status == 200) {
          var img = document.createElement("a");
          img.href = this.base_photo() + data.file;
          img.click();
        }
      } catch (error) {
        alert("Ocurrio un problema!!");
      }
    },

    async exportarFotos() {
      let {
        dni,

        modalidad,
        escuela,
        filial,
        ingresantes,
        universidad_procedencia,
      } = this.filters;

      if (dni) dni = dni.toString();

      modalidad = modalidad ? modalidad.id : 0;

      escuela = escuela ? escuela.id : 0;

      let sede = filial ? filial.id : 0;

      dni = dni ? dni : "";
      ingresantes = ingresantes || false;
      const limit = this.limite;

      //
      universidad_procedencia = universidad_procedencia
        ? universidad_procedencia.nombre
        : "";
      //

      const { data, status } = await axios.get("fotosIgresantesToZip", {
        params: {
          dni,
          limit,
          modalidad,
          escuela,
          sede,
          ingresantes,
          universidad_procedencia,
        },
      });

      if (status == 200) {
        var img = document.createElement("a");
        img.href = this.base_photo() + data.file;
        img.click();
      }
    },

    async exportarExcel() {
      let {
        dni,

        modalidad,
        escuela,
        filial,
        ingresantes,
        universidad_procedencia,
      } = this.filters;

      if (dni) dni = dni.toString();

      modalidad = modalidad ? modalidad.id : 0;

      escuela = escuela ? escuela.id : 0;

      let sede = filial ? filial.id : 0;

      dni = dni ? dni : "";
      ingresantes = ingresantes || false;
      const limit = this.limite;
      //
      universidad_procedencia = universidad_procedencia
        ? universidad_procedencia.nombre
        : "";
      //

      const { data, status } = await axios.get("filterPostulanteToExcel", {
        params: {
          dni,
          limit,
          modalidad,
          escuela,
          sede,
          ingresantes,
          universidad_procedencia,
        },
      });

      if (status == 200) {
        var img = document.createElement("a");
        img.href = this.base_photo() + data.download_excel_file;
        img.click();
      }
    },

    base_photo() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/media/");
      return host;
    },

    filterFilial() {
      const username = this.get_auth.username;
      const result = ["yurimaguas", "contamana", "datem", "requena"].find(
        (item) => item.trim().toLowerCase() == username.trim().toLowerCase()
      );

      if (result) {
        //
        const index = this.sede_data.findIndex(
          (item) =>
            item.nombre.trim().toLowerCase() == result.trim().toLowerCase()
        );

        if (index > -1) {
          this.bloquear_filial = true;
          this.filters.filial = this.sede_data[index];
          this.$emit("filtering", this.filters);
          setTimeout(() => {
            document.getElementById("txtidcodigo").focus();
          }, 10);
        }
      }
    },

    checkIngresantes() {
      this.$emit("filtering", this.filters);
    },

    // sleep time expects milliseconds
    sleep(time) {
      return new Promise((resolve) => setTimeout(resolve, time));
    },

    ParseMoney(val) {
      const formatterPeso = new Intl.NumberFormat("es-PE", {
        currency: "PEN",
        minimumFractionDigits: 3,
      });
      val = Math.round(val * 100) / 100;
      return formatterPeso.format(val);
    },

    async set_modalidad() {
      try {
        const { data } = await axios.get("modalidad");
        this.modalidad_data = data;
      } catch (error) {
        return null;
      }
    },

    async set_escuela() {
      try {
        const { data } = await axios.get("escuela");
        this.escuela_data = data;
      } catch (error) {
        return null;
      }
    },

    async set_sede() {
      try {
        const { data } = await axios.get("cede");
        this.sede_data = data;
      } catch (error) {
        return null;
      }
    },
  },
};
</script>
