<template>
  <section
    class="flex flex-col items-start p-3 shadow-md rounded-lg w-full bg-white"
  >
    <h1 class=" text-xl font-bold text-green-500">
      Programa de Estudios
    </h1>
    <div class="mt-7">
      <h1 class=" text-lg text-blue-500 font-medium">
        Datos del programa de estudios
      </h1>
      <div>
        <span class=" text-xs ml-2">Programa al que postula</span>
        <input
          :value="itemSelectedLocal.escuela_"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
          readonly
          type="text"
        />

        <span class=" text-xs ml-2">Sede/Filial</span>
        <input
          :value="itemSelectedLocal.sede_"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
          readonly
          type="text"
        />

        <span class=" text-xs ml-2">Modalidad</span>
        <input
          :value="itemSelectedLocal.modalidad_"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
