<script>
export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {
      itemSelectedLocal: {},
    };
  },

  mounted() {
    this.$store
      .dispatch("postulante/getPrograma", this.item_selected.id)
      .then((resp) => {
        this.itemSelectedLocal = resp;
      });
  },
};
</script>

