<template>
  <div>
    <div class="flex justify-between">
      <div></div>
      <h1 class="text-lg text-gray-400 font-medium">Postulantes</h1>
    </div>

    <list
      :dataList="get_postulante"
      @edit="edit"
      @filtering="
        filters = $event;
        set_postulante();
      "
      @change_pages="set_postulante()"
      @limits="limitEvent"
      @imprimir="
        dataImpresion = $event;
        index = 0;
        descargarConstancias();
      "
    />

    <ViewData
      @closedialog="dialog = false"
      :item_selected="item_selected"
      :dialog="dialog"
      @actualizar="set_postulante()"
    />
  </div>
</template>

<script>
import List from "./components/List.vue";
import View from "./components/View.vue";
import axios from "@//plugins//axios";

export default {
  components: { List, ViewData: View },

  data() {
    return {
      dialog: false,
      item_selected: null,
      filters: {
        modalidad: null,
        escuela: null,
        dni: "",
        filial: null,
        fecha_ini: null,
        fecha_fin: null,
        ingresantes: null,
      },
      limite: "true",
      esperando_respuesta: false,
      is_mounted: false,
      index: 0,
      dataImpresion: [],
      sede_data: [],
    };
  },

  computed: {
    get_postulante() {
      return this.$store.getters["postulante/get_postulante"];
    },

    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },

    baseMedia() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/");
      return host;
    },
  },

  mounted() {
    this.is_mounted = true;
    this.set_sede().then(() => {
      this.filterFilial();
      this.set_postulante();

      this.is_mounted = false;
    });
  },

  methods: {
    async set_sede() {
      try {
        const { data } = await axios.get("cede");
        this.sede_data = data;
      } catch (error) {
        return null;
      }
    },

    filterFilial() {
      const username = this.get_auth.username;
      const result = ["yurimaguas", "contamana", "datem", "requena"].find(
        (item) => item.trim().toLowerCase() === username.trim().toLowerCase()
      );

      if (result) {
        //
        const index = this.sede_data.findIndex(
          (item) =>
            item.nombre.trim().toLowerCase() === result.trim().toLowerCase()
        );

        if (index > -1) {
          this.filters.filial = this.sede_data[index];
        }
      }
    },

    async descargarConstancias() {
      const escuelaId = this.filters.escuela.id;
      const { data } = await axios.get("reportes/get-constancia-escuela", {
        params: { escuelaId },
      });

      var a = document.createElement("a");
      a.href = this.baseMedia + data.file;
      a.target = "_blank";
      a.click();
    },

    limitEvent(e) {
      this.limite = e;
      this.set_postulante();
    },

    set_postulante() {
      try {
        let dni = this.filters.dni;
        if (dni) dni = dni.toString();

        let modalidad = 0;
        modalidad = this.filters.modalidad ? this.filters.modalidad.id : 0;

        let escuela = 0;
        escuela = this.filters.escuela ? this.filters.escuela.id : 0;

        let sede = 0;
        sede = this.filters.filial ? this.filters.filial.id : 0;

        let ingresantes = false;
        ingresantes = this.filters.ingresantes;

        let universidad_procedencia = this.filters.universidad_procedencia
          ? this.filters.universidad_procedencia.nombre
          : "";

        if (universidad_procedencia)
          universidad_procedencia = universidad_procedencia.toString().trim();

        //
        if (this.esperando_respuesta) return;
        this.esperando_respuesta = true;

        let obj = {
          dni: dni,
          limit: this.limite,
          modalidad,
          escuela,
          sede,
          ingresantes,
          universidad_procedencia,
        };

        this.$store.dispatch("postulante/set_postulante", obj).then(() => {
          this.esperando_respuesta = false;
          if (this.filters.dni !== dni) this.set_postulante();
        });
      } catch (error) {
        if (this.is_mounted) {
          this.esperando_respuesta = true;
          this.$store
            .dispatch("postulante/set_postulante", { dni: "", limit: true })
            .then(() => {
              this.esperando_respuesta = false;
            });
        }
      }
    },

    edit(e) {
      if (this.get_auth.group === "consultor") return;
      this.item_selected = e;
      this.dialog = true;
    },
  },
};
</script>
