<template>
  <div class="flex flex-col">
    <div class=" m-2 flex justify-between">
      <div class=" w-1/2 flex ">
        <div class="w-full">
          <span class=" text-xs">Filtro Dni</span>
          <input
            type="number"
            id="txtdni"
            ref="dni2"
            class="p-3 bg-gray-100 rounded-xl block"
            v-model.trim="dni"
            @keydown.enter="filter"
            autofocus
          />
        </div>
      </div>

      <div class="flex items-end">
        <vs-button class=" focus:outline-none" @click="validarTodo()">
          Validar Todos
        </vs-button>

        <vs-button
          success
          class=" focus:outline-none"
          @click="imprimirValidados"
        >
          Imprimir Validados
        </vs-button>
      </div>
    </div>

    <vs-table striped class=" mt-2">
      <template #header> </template>

      <template #thead>
        <vs-tr>
          <vs-th v-for="(item, i) in columns" :key="i">
            {{ item.text }}
          </vs-th>
        </vs-tr>
      </template>

      <template #tbody>
        <vs-tr v-for="(item, i) in items" :key="i">
          <vs-td @click="$emit('edit', item)" class=" underline cursor-pointer">
            {{ item.id }}
          </vs-td>

          <vs-td>
            {{ item.dni }}
          </vs-td>

          <vs-td>
            {{
              `${item.nombre} ${item.apellido_paterno} ${item.apellido_materno}`
            }}
          </vs-td>

          <vs-td>
            {{ item.modalidad }}
          </vs-td>

          <vs-td>
            {{ item.escuela }}
          </vs-td>

          <vs-td>
            <span
              class=" font-bold"
              :class="
                item.validado
                  ? '   bg-green-500 px-2 text-white rounded-xl'
                  : ' text-red-500'
              "
              >{{ item.validado ? "Si" : "No" }}</span
            >
          </vs-td>

          <vs-td>
            <vs-button
              class="text-xs p-0 m-0 focus:outline-none"
              danger
              @click="deleteItem(item.id)"
            >
              x
            </vs-button>
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No existen datos 😢
      </template>
    </vs-table>
  </div>
</template>

<script>
import axios from "@//plugins//axios";

export default {
  name: "validar-documentos",
  props: {},

  data() {
    return {
      dni: "",
      ver_menu: false,
      columns: [
        {
          name: "id",
          text: "Id",
        },

        {
          name: "dni",
          text: "Dni",
        },

        {
          name: "nombre",
          text: "Nombre",
        },

        {
          name: "modalidad",
          text: "Modalidad al que postula",
        },

        {
          name: "escuela",
          text: "Escuela al que postula",
        },

        {
          name: "validado",
          text: "Validado",
        },

        {
          name: "accion",
          text: "eliminar",
        },
      ],
      items: [],
      dataList: [],
      esperando_respuesta: false,
      itemSelected: {},
    };
  },

  watch: {
    dataList(newValue) {
      if (newValue.length > 0) {
        this.itemSelected = newValue[0];
        this.addItem();
      }
    },
  },

  mounted() {
    setTimeout(() => {
      this.$refs.dni2.focus();
    }, 500);
  },

  computed: {
    base_photo() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/media/");
      return host;
    },
  },

  methods: {
    printRegistro(item) {
      let routeData = this.$router.resolve(`/reporte/${item.id}`);

      window.open(routeData.href, "_blank");
    },

    async imprimirValidados() {
      for (let index = 0; index < this.items.length; index++) {
        const element = this.items[index];
        if (!element.validado) continue;
        this.printRegistro(element);
        await this.sleep(500);
      }
    },

    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },

    async filter() {
      if (this.esperando_respuesta || this.dni == "") return;
      this.esperando_respuesta = true;
      await this.$store
        .dispatch("postulante/get_postulante_full_v2_for_validation", {
          id: this.dni,
          dni: this.dni,
        })
        .then((response) => {
          this.esperando_respuesta = false;
          if (response != null && response != "") {
            this.dataList = response;
          }
        });
    },

    async validarTodo() {
      if (this.items.length <= 0) return;

      const loading = this.$vs.loading({
        color: "succcess",
      });

      try {
        //
        const array = this.items.map((item) => item.id);
        const { data } = await axios.post("validar_requisitos_bulk/", {
          postulante: array,
        });

        if (data.length > 0) {
          data.forEach((element) => {
            const item = this.items.find((item) => item.id == element.id);
            if (item != null) {
              item.validado = element.status;
              return;
            }
          });
          return;
        }
      } catch (error) {
        //
      } finally {
        loading.close();

        this.$store.dispatch("postulante/set_postulante", {
          dni: "",
          limit: true,
        });
      }
    },

    deleteItem(id) {
      this.items = this.items.filter((item) => item.id != id);
    },

    addItem() {
      if (!this.itemSelected.id) return;

      const exist = this.items.findIndex(
        (item) => item.id == this.itemSelected.id
      );
      if (exist > -1) {
        this.$vs.notification({
          title: "Validar Documentos",
          text: "El postulante ya esta en la lista! 😎",
          color: "danger",
        });
        return;
      }

      this.items.push(this.itemSelected);
      this.itemSelected = {};
      this.$refs.dni2.focus();
      this.$refs.dni2.select();
    },

    clickClean() {
      this.dni = "";
      this.$refs.txtdni2.focus();
    },
  },
};
</script>

<style lang="scss" scoped>
.foto {
  width: 240px !important;
  height: 288px !important;
}
</style>
