<template>
  <div>
    <div class="flex">
      <div class="w-full">
        <div v-for="(item, i) in requisito_data" :key="i" class="flex">
          <vs-checkbox success :val="item" v-model="requisitos">
            <template #icon>
              <i class="fas fa-check-double"></i>
            </template>

            <div>{{ item.requisito }}</div>
          </vs-checkbox>

          <div class="flex gap-2 items-center">
            <div
              @click="
                requisitoSelected = item;
                dialogRequisito = true;
              "
              class="text-xs text-blue-500 cursor-pointer"
            >
              <i class="fas fa-eye cursor-pointer"></i>
              Ver
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="mt-5 flex">
      <vs-button @click="dialogconfirm = true" class="focus:outline-none">
        Guardar
      </vs-button>

      <div v-if="imprimir_registro">
        <reportes :item_selected="item_selected" />
      </div>
    </div>

    <div v-if="item_selected" class="flex">
      <DeclaracionJurada :item_selected="item_selected" />
      <ficha-postulante :item_selected="item_selected" />
    </div>

    <vs-dialog width="300px" not-center v-model="dialogconfirm">
      <template #header>
        <h4 class="not-margin">Confirmar Acción</h4>
      </template>

      <div class="con-content">
        <span>Seguro que desea guardar los requisitos?</span>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class="flex">
            <vs-button transparent @click="save_data"> Si </vs-button>
            <vs-button dark transparent @click="dialogconfirm = false">
              Cancelar
            </vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>

    <vs-dialog width="300px" not-center v-model="dialogconfirmValidar">
      <template #header>
        <h4 class="not-margin">Confirmar Acción</h4>
      </template>

      <div class="con-content">
        <span>Seguro que desea validar los datos?</span>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class="flex">
            <vs-button transparent @click="validar_postulante()">
              Si
            </vs-button>
            <vs-button dark transparent @click="dialogconfirmValidar = false">
              Cancelar
            </vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>

    <vs-dialog
      width="800px"
      blur
      not-center
      v-model="dialogRequisito"
      id="dialogRequisito"
      :full-screen="openFullScreen"
    >
      <div v-for="(req, i) in requisito_data" :key="i" class="w-full">
        <div :class="req == requisitoSelected ? 'block' : 'hidden'">
          <div class="flex p-2 w-full">
            <vs-checkbox success :val="req" v-model="requisitos">
              <template #icon>
                <i class="fas fa-check-double"></i>
              </template>

              <div
                class="text-xs w-auto whitespace-nowrap overflow-ellipsis overflow-hidden"
              >
                <b>Validar: </b> {{ req.requisito }}
              </div>
            </vs-checkbox>

            <div class="flex items-center text-xs">
              <vs-button
                class="focus:outline-none"
                icon
                size="small"
                color="#000"
                border
                @click="changeDocument = !changeDocument"
              >
                <i class="fas fa-exchange-alt mr-2"></i>
                Cambiar Documento
              </vs-button>

              <div v-if="changeDocument" class="p-2 bg-gray-100 rounded-lg">
                <input
                  type="file"
                  accept="application/pdf"
                  class="file"
                  id="file"
                  ref="file"
                  @change="cargarNuevoDocumento"
                />

                <label
                  :disabled="!newRequisito"
                  class="btn btn-tertiary btn-sm"
                  :class="
                    newRequisito
                      ? 'cursor-pointer text-blue-500'
                      : 'cursor-not-allowed'
                  "
                  @click="setNewRequisito"
                >
                  <i class="fas fa-upload"></i>
                  Subir
                </label>
              </div>

              <div class="absolute top-10 right-5 flex items-center gap-2">
                <a
                  :href="getWathsAppMessage"
                  target="_blank"
                  class="text-base text-green-500 cursor-pointer"
                >
                  <i class="fab fa-whatsapp"></i>
                </a>

                <vs-button
                  v-if="getRequisitoIndex == requisito_data.length - 1"
                  @click="dialogconfirm = true"
                  class="focus:outline-none"
                >
                  Guardar y cerrar
                </vs-button>
              </div>
            </div>
          </div>

          <div class="mt-5 mb-2">
            <div class="flex justify-between items-center">
              <div class="flex justify-end gap-5">
                <div
                  @click="setPrevRequisito"
                  class="text-xs"
                  :class="
                    i > 0
                      ? 'cursor-pointer hover:underline text-blue-500'
                      : 'cursor-not-allowed text-gray-400'
                  "
                >
                  <i class="fas fa-arrow-left"></i>
                  Requisito Aterior
                </div>

                <div
                  @click="setNextRequisito"
                  class="text-xs"
                  :class="
                    i < requisito_data.length - 1
                      ? 'cursor-pointer hover:underline text-blue-500'
                      : 'cursor-not-allowed text-gray-400'
                  "
                >
                  <i class="fas fa-arrow-right"></i>
                  Siguiente Requisito
                </div>
              </div>
            </div>
          </div>

          <keep-alive>
            <div :class="openFullScreen ? 'h-screen-2' : 'h-700'">
              <iframe
                :src="getDocument"
                id="iframe"
                class="w-full h-full"
              ></iframe>
            </div>
          </keep-alive>
        </div>

        <div class="absolute bottom-0 mb-5 ml-2">
          <div
            style="background-color: #404040"
            class="flex justify-between items-center mt-2.5 p-3 text-white rounded cursor-pointer"
            @click="openFullScreen = !openFullScreen"
          >
            <div class="flex justify-end">
              <div class="flex items-center text-xs">
                <i class="fas fa-expand-alt mr-2"></i>
                {{
                  !openFullScreen
                    ? "Ver en pantalla completa"
                    : "Cerrar pantalla completa"
                }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </vs-dialog>
  </div>
</template>

<script>
import axios from "@//plugins//axios";
import Reportes from "./reportes/Reportes.vue";
import DeclaracionJurada from "@//views//Reportes//DeclaracionJurada.vue";
import FichaPostulante from "../../../Reportes/FichaPostulante.vue";
export default {
  components: { Reportes, DeclaracionJurada, FichaPostulante },
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {
      requisito_data: [],
      requisitos: [],
      dialogconfirm: false,
      dialogconfirmValidar: false,
      imprimir_registro: false,
      dialogRequisito: false,
      requisitoSelected: {},
      changeDocument: false,
      newRequisito: null,
      openFullScreen: false,
    };
  },

  async mounted() {
    await this.set_requisitos();
  },

  computed: {
    getDocument() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/");
      host = host.substring(0, host.length - 1);

      host += `${this.requisitoSelected.file}`;
      return `https://docs.google.com/gview?url=${host}&embedded=true`;
    },

    getRequisitoIndex() {
      return this.requisito_data.findIndex(
        (item) => item.requisito_id == this.requisitoSelected.requisito_id
      );
    },

    getWathsAppMessage() {
      let message = `"Dirección de Admisión, Universidad Nacional de la Amazonia Peruana"`;
      message += `"Hola ${this.item_selected.nombre} ${this.item_selected.apellido_paterno} ${this.item_selected.apellido_materno}, tu documentación ha sido validada satisfactoriamente."`;
      return `https://wa.me/51${this.item_selected.celular}?text=${message}`;
    },
  },

  methods: {
    async save_data() {
      const loading = this.$vs.loading({
        color: "succcess",
      });
      this.dialogconfirm = false;

      try {
        let final = [];

        this.requisito_data.forEach((element) => {
          let obj = {
            postulante: this.item_selected.id,
            requisito: element.requisito_id,
            success: false,
          };

          let index = this.requisitos.findIndex(
            (item) => item.requisito == element.requisito
          );

          if (index > -1) obj.success = true;
          final.push(obj);
        });

        const { data } = await axios.post("save_requisito_postulante/", final);

        if (data) {
          this.$vs.notification({
            title: "Requisitos",
            text: "Los cambios se realizaron Correctamente! 😎",
            color: "success",
          });

          await this.set_requisitos();
          this.validarRequisitLocalmente(this.imprimir_registro);
        } else {
          this.$vs.notification({
            title: "Requisitos",
            text: "No se pudo realizar la acción! 😢",
            color: "warning",
          });
        }
      } catch (error) {
        //
        this.$vs.notification({
          title: "Requisitos",
          text: "Ocurrio un error! 📛 ",
          color: "danger",
        });
      } finally {
        loading.close();
        this.dialogRequisito = false;
      }
    },

    validarRequisitLocalmente(validado) {
      this.$store.dispatch("postulante/validar_localmente", {
        id: this.item_selected.id,
        is_valid: validado,
      });
    },

    async set_requisitos() {
      var is_valid = false;
      try {
        const postulante = this.item_selected.id;
        const { data } = await axios.get("get_requisito_by_modalidad/", {
          params: {
            postulante: postulante,
          },
        });

        is_valid = true;

        this.requisito_data = data;

        this.requisitos = data.filter((row) => {
          if (row.success) return row;
        });

        is_valid = data.filter((item) => item.success == false).length <= 0;

        this.imprimir_registro = false;
        this.imprimir_registro = is_valid;
      } catch (error) {
        this.imprimir_registro = false;
      }
    },

    cargarNuevoDocumento(event) {
      const file = event.target.files[0];
      // getbase64
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.newRequisito = reader.result;
      };
    },

    setClearInputFile() {
      if (this.$refs.file) this.$refs.file.value = "";
      this.newRequisito = null;
      this.changeDocument = false;
    },

    setNextRequisito() {
      this.setClearInputFile();

      let index = this.requisito_data.findIndex(
        (item) => item.requisito_id == this.requisitoSelected.requisito_id
      );

      if (index > -1) {
        let next = this.requisito_data[index + 1];
        if (next) this.requisitoSelected = next;
      }
    },

    setPrevRequisito() {
      this.setClearInputFile();

      let index = this.requisito_data.findIndex(
        (item) => item.requisito_id == this.requisitoSelected.requisito_id
      );

      if (index <= -1) return;

      let prev = this.requisito_data[index - 1];
      if (prev) this.requisitoSelected = prev;
    },

    async setNewRequisito() {
      const requisitoObject = Object.assign({}, this.requisitoSelected, {
        documento: this.newRequisito,
      });

      const response = await this.$store.dispatch(
        "postulante/save_postulante_requisito",
        {
          postulante_id: this.item_selected.id,
          requisito: requisitoObject,
        }
      );

      this.requisitoSelected.file = response[0].file;
    },
  },
};
</script>

<style>
.h-700 {
  height: 700px;
}

/* h full screen */
.h-screen-2 {
  height: calc(100vh - 160px) !important;
}
</style>
