<template>
  <section
    class="flex flex-col items-start p-3 shadow-md rounded-lg w-full bg-white"
  >
    <h1 class=" text-xl font-bold text-green-500">
      Caracteristicas Generales
    </h1>

    <div class="mt-7">
      <div class="mt-7">
        <vs-input
          label="Estado Civil"
          :value="itemSelectedLocal.estado_civil_"
          class="w-full capitalize"
          readonly
        />

        <vs-input
          label="Dependencia Familiar"
          :value="itemSelectedLocal.familiar_"
          class="w-full mt-7 capitalize"
          readonly
        />

        <span class=" text-xs ml-2">Limitacion Permanente</span>
        <input
          :value="itemSelectedLocal.limitacion_permanente_"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
          readonly
          type="text"
        />

        <vs-input
          label="Trabaja"
          :value="itemSelectedLocal.trabaja_ || 'No'"
          class="w-full mt-7"
          readonly
        />

        <vs-input
          label="Actividad Económica"
          :value="itemSelectedLocal.actividad_economica_"
          class="w-full mt-7 capitalize"
          readonly
        />

        <span class=" text-xs ml-2">Tipo de Trabajo</span>
        <input
          :value="itemSelectedLocal.tipo_trabajo_"
          class="w-full bg-gray-100 p-2 px-3 rounded-xl capitalize"
          readonly
          type="text"
        />

        <vs-input
          label="Pertenece a etnia"
          :value="itemSelectedLocal.es_etnia ? 'Si' : 'No'"
          class="w-full mt-7 capitalize"
          readonly
        />
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {
      itemSelectedLocal: {},
    };
  },

  mounted() {
    this.$store
      .dispatch("postulante/getCaracteristicasGenerales", this.item_selected.id)
      .then((resp) => {
        this.itemSelectedLocal = resp;
      });
  },
};
</script>

